import React from 'react';
import Title from '../components/Title';
import Slider from 'react-animated-slider';
import 'react-animated-slider/build/horizontal.css';
import TextBlock from './TextBlocks/Home.json';
const lang = window.localStorage["language"] || "en";

var slides = [];
for(let quote of TextBlock.quotes){
  slides.push({
    "description":quote[lang]
  })
}
console.log(slides)

class Quotes extends React.Component {
  constructor (props) {
    super(props);
    this.updateIndex = this.updateIndex.bind(this);
    this.state = {
      index:0
    }
  }

  updateIndex(event) {
    this.setState({index: event.slideIndex})
  }


  render () {
    return (
      <div className="Quotes">
      <Slider autoplay="4000" infinite="true" previousButton="" nextButton="" onSlideChange={this.updateIndex}>
  {slides.map((slide, index) => <div key={index}>
    <div>{slide.description}</div>
  </div>)}
</Slider>
<div className="slideCounters">
  {
    slides.map((slide, index)=> <div key={index} index={index} className={index === this.state.index? "active" : ""}></div>)
  }
</div>
</div>
    )
  }
}

export default class Home extends React.Component {
  render () {
    return (
      <main>
        <Title background='./imgs/titles/home.png'>
          <div>Parenting Across Borders</div>
          <i style={{fontSize:"0.6em"}}>With and For International Parents</i>
        </Title>
        <div className="content">
          <h2>Quotes From Mothers</h2>
          <Quotes/>
          {
            TextBlock.blocks.map((block,index)=>{
              let b = block[lang];
              return (<div key={index}>
                <h2>{b.title}</h2>
                <hr></hr>
                <p dangerouslySetInnerHTML={{ __html: b.content }}></p>
              </div>)
            })
          }
          
        </div>
      </main>
    )
  }
}