import React from 'react';
import Title from '../components/Title';
import Socials from '../components/Socials';

export default class Contact extends React.Component {
  render () {
    return (
      <main>
      <Title background="./imgs/titles/home.png">Contact</Title>
      <div class="content">
        <Socials includeText/>
      </div>
      </main>
    )
  }
}